
.cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom: 12px;
  height: 30px;
}
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  font-size: 24px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease .inner {
  position: relative;
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-transition {
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease.move-transition .inner {
  position: relative;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner {
  position: absolute;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-decrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-leave-active .inner {
  position: absolute;
  left: -24px;
  top: -24px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  font-size: 10px;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding-top: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}


